import { Component } from '@angular/core';
import { NavController, NavParams, AlertController, PopoverController } from 'ionic-angular';

@Component({
  selector: 'page-contact',
  templateUrl: 'contact.html'
})

export class ContactPage {
  showAddProduto = false;
  verdurasTrue = true;
  frutasTrue = true;
  bebidasTrue = true;
  higieneTrue = true;
  produto = {
    nome: '',
    preco: 0,
    unidades: 0,
    categoria: '',
    observacao: ''
  }

  produtoVerdura = {
    nome: '',
    preco: 0,
    unidades: 0,
    categoria: '',
    observacao: ''
  }

  produtoFruta = {
    nome: '',
    preco: 0,
    unidades: 0,
    categoria: '',
    observacao: ''
  }

  produtoBebida = {
    nome: '',
    preco: 0,
    unidades: 0,
    categoria: '',
    observacao: ''
  }

  produtoHigiene = {
    nome: '',
    preco: 0,
    unidades: 0,
    categoria: '',
    observacao: ''
  }

  infoprodutos = [];
  produtosVerduras = [];
  produtosFrutas = [];
  produtosBebidas = [];
  produtosHigiene = [];

  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public alertCtrl: AlertController,
              public popoverCtrl: PopoverController) {
  }

  message(b){
    this.infoprodutos.push(this.produto);

    if(this.produto.categoria == "Verduras"){
      this.produtosVerduras.push(this.produto);
    }
    if(this.produto.categoria == "Frutas"){
      this.produtosFrutas.push(this.produto);
    }
    if(this.produto.categoria == "Bebidas"){
      this.produtosBebidas.push(this.produto);
    }
    if(this.produto.categoria == "Higiene"){
      this.produtosHigiene.push(this.produto);
    }

    let alert = this.alertCtrl.create({
      title: this.produto.nome,
      subTitle: 'produto registrado', 
      buttons: ['ok']
    });

    this.produto = {
      nome: '',
      preco: 0,
      unidades: 0,
      categoria: '',
      observacao: ''
    }

    this.produtoVerdura = {
      nome: '',
      preco: 0,
      unidades: 0,
      categoria: '',
      observacao: ''
    }

    this.produtoFruta = {
      nome: '',
      preco: 0,
      unidades: 0,
      categoria: '',
      observacao: ''
    }

    this.produtoBebida = {
      nome: '',
      preco: 0,
      unidades: 0,
      categoria: '',
      observacao: ''
    }

    this.produtoHigiene = {
      nome: '',
      preco: 0,
      unidades: 0,
      categoria: '',
      observacao: ''
    }

    alert.present();
    this.showAddProduto = !b
    console.log(this.produtoBebida);
  }

  AddItem(b){
      this.showAddProduto = !b;
      console.log(this.infoprodutos);
  }

  showVerduras(b){
    this.verdurasTrue = !b;
  }

  showFrutas(b){
    this.frutasTrue = !b;
  }

  showBebidas(b){
    this.bebidasTrue = !b;
  }

  showHigiene(b){
    this.higieneTrue = !b;
    console.log(this.higieneTrue)
  }

  DeleteItem(str){
    for(var i=0;i<this.infoprodutos.length;i++){
      if(str === this.infoprodutos[i]){
        this.infoprodutos.splice(i,1)
    }}
    for(var i=0;i<this.produtosVerduras.length;i++){
      if(str === this.produtosVerduras[i]){
        this.produtosVerduras.splice(i,1)
    }}
    for(var i=0;i<this.produtosFrutas.length;i++){
      if(str === this.produtosFrutas[i]){
        this.produtosFrutas.splice(i,1)
    }}
    for(var i=0;i<this.produtosBebidas.length;i++){
      if(str === this.produtosBebidas[i]){
        this.produtosBebidas.splice(i,1)
    }}
    for(var i=0;i<this.produtosHigiene.length;i++){
      if(str === this.produtosHigiene[i]){
        this.produtosHigiene.splice(i,1)
    }}
  }
}


