import { Component } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';

//import { FavoritosComponent } from '../../components/favoritos/favoritos';

@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage {
  searchQuery: string = '';
  items: string[];
  fav = [{
    'title': '',
    'fav': false,
  }
  ];

  constructor(public navCtrl: NavController, public alertCtrl: AlertController) {
    this.initializeItems();
    var aux = JSON.stringify(this.favoritos);
    sessionStorage.setItem('AboutPage', aux);
  }

  tabinterno = true;

  initializeItems(){
    if(this.tabinterno){
      this.items = [
      'item10',
      'item12',
      'item13',
      'item14',
      'item15',
      ];
    }

    if(!this.tabinterno){
      this.items = [
      'item10000',
      'item12000',
      'item13000',
      'item14000',
      'item15000',
      ];
    }

    this.fav = [
    {
      'title': 'item10',
      'fav': false,
    },
    {
      'title': 'item12',
      'fav': false,
    },
    {
      'title': 'item13',
      'fav': false,
    },{
      'title': 'item14',
      'fav': false,
    },
    {
      'title': 'item15',
      'fav': false,
    },
        {
      'title': 'item10000',
      'fav': false,
    },
    {
      'title': 'item12000',
      'fav': false,
    },
    {
      'title': 'item13000',
      'fav': false,
    },
    {
      'title': 'item14000',
      'fav': false,
    },
    {
      'title': 'item15000',
      'fav': false,
    }
    ];

  }

  favoritos = [];

  addfavoritos(str){
    for (var i = 0; i < this.fav.length; ++i) {
      if(this.fav[i].title == str){
        if (this.fav[i].fav == false && this.favoritos.findIndex(n => n === this.fav[i].title) === -1) {
          this.fav[i].fav = true;
          this.favoritos.push(this.fav[i].title);
        }else{
          this.fav[i].fav = false;
          this.favoritos.splice(this.favoritos.findIndex(n => n === this.fav[i].title),1);
          console.log('deu bom');
        }
        console.log(this.fav[i].fav);
        console.log(this);
      }
    }

    console.log(this.favoritos);
    var aux = JSON.stringify(this.favoritos);
    sessionStorage.setItem('AboutPage', aux);

    let alert = this.alertCtrl.create({
    title: str,
    subTitle: 'Este item foi favoritado',
    buttons: ['OK']});
    alert.present();
  }









  removefavoritos(str){
    for (var i = 0; i < this.fav.length; ++i) {
      if(this.fav[i].title == str){
        if (this.fav[i].fav == false) {
          this.fav[i].fav = true;
          this.favoritos.push(this.fav[i].title);
        }else{
          this.fav[i].fav = false;
          this.favoritos.splice(i,1);
          this.fav.splice(i,1);
          console.log('deu bom');
        }
        console.log(this.fav[i].fav);
        console.log(this);
      }
    }

    console.log(this.favoritos);
    var aux = JSON.stringify(this.favoritos);
    sessionStorage.setItem('AboutPage', aux);

    let alert = this.alertCtrl.create({
    title: str,
    subTitle: 'Este item foi favoritado',
    buttons: ['OK']});
    alert.present();
  }












  tabinternotroca(b){
    if(!this.tabinterno){
      this.tabinterno = !b;
      this.initializeItems();
    }}

    tabinternotroca2(b){
      if(this.tabinterno){
        this.tabinterno = !b;
        this.initializeItems();
      }}

      getItems(ev: any) {
    // Reset items back all of the items
    this.initializeItems();

    //set val to the value of the searchbar
    let val = ev.target.value;

    // if the value is an empty string dont filter items
    if (val && val.trim() != ""){
      this.items = this.items.filter((item) => {
        return (item.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }

  }

  text;

  message(it){

    if(it == 'item10'){
      this.text = {
        title: 'item10',
        subTitle: 'Talvez esta substância provenha de uma fonte animal, portanto, tenha cuidado ao consumi-la',
      }
    }

    if(it == 'item12'){
      this.text = {
        title: 'Item12',
        subTitle: 'Talvez esta substância provenha de uma fonte animal, portanto, tenha cuidado ao consumi-la',
      }
    }

    if(it == 'item13'){
      this.text = {
        title: 'Item13',
        subTitle: 'Talvez esta substância provenha de uma fonte animal, portanto, tenha cuidado ao consumi-la',
      }
    }

    if(it == 'item14'){
      this.text = {
        title: 'Item14',
        subTitle: 'Talvez esta substância provenha de uma fonte animal, portanto, tenha cuidado ao consumi-la',
      }
    }

    if(it == 'item15'){
      this.text = {
        title: 'Item15',
        subTitle: 'Talvez esta substância provenha de uma fonte animal, portanto, tenha cuidado ao consumi-la',
      }
    }
    if(it == 'item10000'){
      this.text = {
        title: 'item10000',
        subTitle: 'Talvez esta substância provenha de uma fonte animal, portanto, tenha cuidado ao consumi-la',
      }
    }

    if(it == 'item12000'){
      this.text = {
        title: 'Item12000',
        subTitle: 'Talvez esta substância provenha de uma fonte animal, portanto, tenha cuidado ao consumi-la',
      }
    }

    if(it == 'item13000'){
      this.text = {
        title: 'Item13000',
        subTitle: 'Talvez esta substância provenha de uma fonte animal, portanto, tenha cuidado ao consumi-la',
      }
    }

    if(it == 'item14000'){
      this.text = {
        title: 'Item14000',
        subTitle: 'Talvez esta substância provenha de uma fonte animal, portanto, tenha cuidado ao consumi-la',
      }
    }

    if(it == 'item15000'){
      this.text = {
        title: 'Item15000',
        subTitle: 'Talvez esta substância provenha de uma fonte animal, portanto, tenha cuidado ao consumi-la',
      }
    }

    let alert = this.alertCtrl.create({
      title: this.text.title,
      subTitle: this.text.subTitle,
      buttons: ['OK']
    });

    alert.present();
  }

  messagefav(it){

    this.text = {
      title: it,
      subTitle: 'Este item foi favoritado'
    }

    let alert = this.alertCtrl.create({
      title: this.text.title,
      subTitle: this.text.subTitle,
      buttons: ['OK']
    });
    alert.present();
    var aux = JSON.stringify(this.favoritos);
    sessionStorage.setItem('AboutPage', aux);
  }

}


